'use client';
import React, { useState, useEffect, useRef, useLayoutEffect } from 'react';
import styles from './HeroCarousel.module.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Image from 'next/image';


gsap.registerPlugin(ScrollTrigger);

const HeroCarousel = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [progress, setProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(true);
  const progressInterval = useRef<NodeJS.Timeout | null>(null);
  const videoRefs = useRef<(HTMLVideoElement | null)[]>([]);
  const fgVideoRefs = useRef<(HTMLVideoElement | null)[]>([]);
  const heroRef = useRef<HTMLDivElement>(null);
  const overlayRef = useRef<HTMLDivElement>(null);
  const gridRef = useRef<HTMLDivElement>(null);

  const carouselItems = [
    {
      bgVideo: '/background.mp4',
      fgVideo: '/hero_video.mp4',
      title: 'CREATIVE ALCHEMY',
      nextTitle: 'TECHNOLOGY TRINITY',
    },
    {
      bgVideo: '/hero_video.mp4',
      fgVideo: '/video.mp4',
      title: '3 IS THE MAGIC NUMBER',
      nextTitle: 'CREATIVE ALCHEMY',
    },
    {
      bgVideo: '/video.mp4',
      fgVideo: '/hero_video2.mp4',
      title: 'SHAKE UP THE SENSES',
      nextTitle: 'TIME IS AN ILLUSION',
    },
  ];

  const articles = [
    { id: 1, title: "Cartier Celebrates 100 Years of Trinity", image: "/watches.jpg" },
    { id: 2, title: "Trinity: A Contemporary Icon", image: "/caps.jpg" },
    { id: 3, title: "All Linked by Trinity", image: "/perfumes.jpg" },
    { id: 4, title: "Trinity, A Symbol of Unity", image: "/sunglasses.jpg" },
    { id: 5, title: "Trinity 100, The Journey of Trinity", image: "/jackets.jpg" }
  ];

  const startProgress = () => {
    if (progressInterval.current) clearInterval(progressInterval.current);
    setProgress(0);
    progressInterval.current = setInterval(() => {
      setProgress(prev => {
        if (prev >= 100) {
          handleNext();
          return 0;
        }
        return prev + (100 / (8 * 10));
      });
    }, 100);
  };

  const handleNext = () => {
    setActiveIndex(prev => (prev + 1) % carouselItems.length);
  };

  const handlePrev = () => {
    setActiveIndex(prev => (prev - 1 + carouselItems.length) % carouselItems.length);
  };

  useEffect(() => {
    if (isPlaying) startProgress();
    return () => {
      if (progressInterval.current) clearInterval(progressInterval.current);
    };
  }, [isPlaying, activeIndex]);

  useEffect(() => {
    const safePlay = (video: HTMLVideoElement | null) => {
      if (!video) return;
      video.currentTime = 0;
      video.play().catch(console.warn);
    };

    videoRefs.current.forEach(v => v && (v.currentTime = 0));
    fgVideoRefs.current.forEach(v => v && (v.currentTime = 0));
    safePlay(videoRefs.current[activeIndex]);
    safePlay(fgVideoRefs.current[activeIndex]);
  }, [activeIndex]);

  useLayoutEffect(() => {
    if (!overlayRef.current || !gridRef.current) return;

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: overlayRef.current,
        start: 'top bottom',
        end: 'top top',
        scrub: 1,
        pin: true,
      }
    });

    tl.to(overlayRef.current, {
      scaleY: 1.5,
      borderRadius: 0,
      ease: 'power2.inOut',
      transformOrigin: 'center center',
    });

    const items = gridRef.current.querySelectorAll(`.${styles.sectionItem}`);
    items.forEach((item, index) => {
      tl.to(item, {
        top: `${10 + index * 18}%`,
        height: 220,
        ease: 'power2.out'
      }, '<');
    });
  }, []);

  return (
    <div style={{ overflowX: 'hidden' }}>
      <div className={styles.heroWrapper}>
        <section ref={heroRef} className={styles.heroCarousel}>
          <div className={styles.bgCarousel}>
            {carouselItems.map((item, index) => (
              <div key={`bg-${index}`} className={`${styles.videoContainer} ${index === activeIndex ? styles.active : ''}`}>
                <video
                  ref={el => { videoRefs.current[index] = el }}
                  className={styles.video}
                  src={item.bgVideo}
                  playsInline
                  muted
                  loop
                />
              </div>
            ))}
          </div>

          <div className={styles.fgCarousel}>
            {carouselItems.map((item, index) => (
              <div key={`fg-${index}`} className={`${styles.fgVideoContainer} ${index === activeIndex ? styles.active : ''}`}>
                <video
                  ref={el => { fgVideoRefs.current[index] = el }}
                  className={styles.fgVideo}
                  src={item.fgVideo}
                  playsInline
                  muted
                  loop
                />
              </div>
            ))}
          </div>

          <div className={styles.textContent}>
            <h1 className={styles.title}>{carouselItems[activeIndex].title}</h1>
            <div className={styles.cta}>
              <p className={styles.caption}>ALL CHAPTERS</p>
              <p className={styles.caption}>READ ARTICLE</p>
            </div>
          </div>
        </section>

        <div ref={overlayRef} className={styles.overlaySection}>
          <h2>Discover More</h2>
          <div ref={gridRef} className={styles.sectionGrid}>
            {articles.map((article, i) => (
              <div key={article.id} className={styles.sectionItem}>
  <div style={{ position: 'relative', width: '100%', height: '100%' }}>
    <Image
      src={article.image}
      alt={article.title}
      fill
      style={{
        objectFit: 'cover',
        borderRadius: 'inherit'
      }}
    />
  </div>
  <p style={{
    position: 'absolute',
    bottom: 0,
    background: 'rgba(255,255,255,0.7)',
    width: '100%',
    textAlign: 'center',
    fontWeight: 'bold',
    zIndex: 2
  }}>{article.title}</p>
</div>

            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default HeroCarousel;
